@use 'base/variables' as v;
@use 'base/mixins' as m;

.tabs {

    @include m.tablet {
        display: flex;
    }
    
    button {
        background-color: v.$secondary-color-1;
        border: .2rem solid v.$secondary-color-1;
        color: v.$white;
        padding: 1rem;
        display: block;
        width: 100%;
        margin-bottom: 1rem;
        text-transform: uppercase;
        font-weight: v.$bold; 
        transition: background-color 300ms ease;

        &:hover {
            cursor: pointer;
            background-color: darken(v.$secondary-color-1, 10%);
        }
        &.actual {
            background-color: v.$white;
            color: v.$secondary-color-1;
        }
    }
    
}