@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario {
    margin-top: 5rem;
    width: 100%;
}

.campo {
    display: flex;
    margin-bottom: 2rem;
    align-items: center;
    label {
        flex: 0 0 12rem;
    }
    input {
        flex: 1;
        border: none;
        padding: calc( v.$separacion / 5);
        border-radius: v.$border-radius-3;
    }

    :disabled{
        background-color: lighten(v.$black, $amount: 30%);
        color: v.$white;
        cursor: not-allowed;
    }
}