// Tipografía
$fuente-principal: 'Montserrat', sans-serif;

// Colors
/// Text Colors
$black: #000;
$white: #FFF;
$light: #f7f7f9;
$gray: #f6f6f9;
$orange-dark: #df5a26;
$red: rgb(255, 0, 0);
$green: rgb(0, 128, 0);

/// Primary Colors
$primary-color-1: #1E23E6;
$primary-color-2: #FF692D;

/// Secondary colors
$secondary-color-1: #001482;
$secondary-color-2: #64B9CD;
$secondary-color-3: #64B95A;
$secondary-color-gradient: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(255,105,45,1) 0%, rgba(30,35,230,1) 100%);

// Border Radius
$border-radius-1: 0.4rem;
$border-radius-2: 0.8rem;
$border-radius-3: 1.2rem;

// Border Shadow
$box-shadow: 0 0 1rem $black;

// Padding
$padding-1: 8rem;
$padding-2: 2.4rem;
$padding-3: 1.8rem;

// Tamaño de Media Queries
$telefono: 480px;
$tablet: 768px;
$desktop: 1024px;

// Margins
$margin-top: 3rem;

// Fuentes
$delgada: 300;
$regular : 400;
$bold: 700;
$black-font: 900;

$separacion: 5rem;