@use 'base/variables' as v;
@use 'base/mixins' as m;

.listado-tipo {
    display: grid;
    gap: 2rem;

    @include m.tablet {
        @include m.grid(2,2rem);
    }
    .tipo {
        background-color: v.$secondary-color-1;
        border: .2rem solid v.$secondary-color-1;
        padding: 1rem;
        border-radius: v.$border-radius-1;
        transition: transform;
        transition-duration: 300ms;
        text-align: center;

        &.seleccionado {
            background-color: v.$white;

            p {
                color: v.$secondary-color-1;
            }
        }

        &.ocultar {
            visibility: hidden;
        }
        
        &:hover {
            transform: scale(1.1);
        }
        p {
            color: v.$white;
            margin: 0 ;
        }
    }
}

.listado-subtipo {

