@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-app {
    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        min-height: 100vh;
    }

    .imagen {
        height: 20rem;
        background-image: url(../img/icon/Elementos-de-marca/fondovideo2-Mesa-de-trabajo-1.webp);
        background-size: cover;
        background-position: center center;

        @include m.tablet {
            height: auto;
        }
    }

    .app {
        width: 95%;
        margin: 0 auto;
        padding: 3rem 0;
        @include m.tablet {
            overflow: scroll;
        }
    }
}

.barra {
    margin: v.$separacion 0;

    @include m.tablet {
        display: flex;       
        justify-content: space-between;
        align-items: center
    }

    .boton {
        @include m.boton(v.$black,v.$white);
        margin: 0;
    }

}

.barra-importaciones {
    margin-bottom: v.$separacion;

    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        
    }

    .boton {
        margin: 0 0 1rem 0;
    }
}