@use 'base/variables' as v;
@use 'base/mixins' as m;

.alerta {
    padding: calc(v.$separacion / 4);
    color: v.$white;
    width: 100%;
    display: block;
    margin-bottom: 2rem;
    text-align: center;
    font-weight: v.$bold;
    text-transform: uppercase;
    border-radius: v.$border-radius-1;

    &.error {
        background-color: v.$red;
    }

    &.exito {
        background-color: v.$green;
    }
}